

import axios from 'axios';
import Vue from 'vue';

import Header from '@/components/Header.vue';
import Navigation from '@/components/Navigation.vue';
import Utils from '@/utils';

export default Vue.extend({
    name: 'Login',
    components: {
        Header,
        Navigation,
    },
    data() {
        return {
            username: '' as string,
            password: '' as string,
            password_showing: false,
        }
    },
    methods: {
        async login() {

            // 
            if (this.username === '' || this.password === '') return;

            try {

                // 
                const response = await Vue.axios.post('/users/token', new URLSearchParams({
                    username: this.username,
                    password: this.password,
                }));

                // 
                console.log('Login successful.');
                console.log(response.data);
                Utils.saveAccessToken(response.data.access_token);

                // 
                this.$message.success('');
                await this.$router.push({path: '/settings/account'});

            } catch (error) {

                // 
                if (axios.isAxiosError(error) && error.response && error.response.status === 401) {

                    console.log('Failed to login.');
                    console.log(error.response.data);

                    //  Snackbar 
                    switch ((error.response.data as any).detail) {
                        case 'Incorrect username': {
                            this.$message.error('');
                            break;
                        }
                        case 'Incorrect password': {
                            this.$message.error('');
                            break;
                        }
                        default: {
                            this.$message.error(`(HTTP Error ${error.response.status})`);
                            break;
                        }
                    }
                }
            }
        }
    }
});

