

import Vue from 'vue';

import Base from '@/views/Settings/Base.vue';
import Utils from '@/utils';

export default Vue.extend({
    name: 'SettingsGeneral',
    components: {
        Base,
    },
    data() {
        return {

            // 
            tv_streaming_quality: [
                {'text': '1080p (60fps) 13.24GB / 7.2Mbps', 'value': '1080p-60fps'},
                {'text': '1080p 12.31GB / 5.1Mbps', 'value': '1080p'},
                {'text': '810p 11.92GB / 4.2Mbps', 'value': '810p'},
                {'text': '720p 11.33GB / 3.0Mbps', 'value': '720p'},
                {'text': '540p 11.00GB / 2.2Mbps', 'value': '540p'},
                {'text': '480p 10.74GB / 1.6Mbps', 'value': '480p'},
                {'text': '360p 10.40GB / 0.9Mbps', 'value': '360p'},
                {'text': '240p 10.23GB / 0.5Mbps', 'value': '240p'},
            ],

            // 
            panel_display_state: [
                {'text': '', 'value': 'RestorePreviousState'},
                {'text': '', 'value': 'AlwaysDisplay'},
                {'text': '', 'value': 'AlwaysFold'},
            ],

            // 
            tv_panel_active_tab: [
                {'text': '', 'value': 'Program'},
                {'text': '', 'value': 'Channel'},
                {'text': '', 'value': 'Comment'},
                {'text': 'Twitter ', 'value': 'Twitter'},
            ],

            // 
            capture_save_mode: [
                {'text': '', 'value': 'Browser'},
                {'text': 'KonomiTV ', 'value': 'UploadServer'},
                {'text': 'KonomiTV ', 'value': 'Both'},
            ],

            // 
            capture_caption_mode: [
                {'text': '', 'value': 'VideoOnly'},
                {'text': '', 'value': 'CompositingCaption'},
                {'text': '', 'value': 'Both'},
            ],

            //  (KonomiTV-Settings.json) 
            import_settings_file: null as File | null,

            // 
            //  v-model  binding 
            settings: (() => {
                // 
                const settings = {}
                const settings_keys = [
                    'tv_streaming_quality',
                    'low_latency_mode',
                    'show_superimpose_tv',
                    'panel_display_state',
                    'tv_panel_active_tab',
                    'capture_save_mode',
                    'capture_caption_mode',
                ];
                for (const setting_key of settings_keys) {
                    settings[setting_key] = Utils.getSettingsItem(setting_key);
                }
                return settings;
            })(),
        }
    },
    watch: {
        // settings 
        settings: {
            deep: true,
            handler() {
                // settings  LocalStorage 
                for (const [setting_key, setting_value] of Object.entries(this.settings)) {
                    Utils.setSettingsItem(setting_key, setting_value);
                }
            }
        }
    },
    methods: {

        // 
        exportSettings() {

            // JSON  LocalStorage 
            // "KonomiTV-Settings"  JSON 
            const settings_json = localStorage.getItem('KonomiTV-Settings') || JSON.stringify(Utils.default_settings);

            //  Blob KonomiTV-Settings.json 
            const settings_json_blob = new Blob([settings_json], {type: 'application/json'});
            Utils.downloadBlobData(settings_json_blob, 'KonomiTV-Settings.json');
            this.$message.success('');
        },

        // 
        async importSettings() {

            // 
            if (this.import_settings_file === null) {
                this.$message.error('');
                return;
            }

            try {

                //  JSON 
                // 
                const settings = JSON.parse(await this.import_settings_file.text());

                // LocalStorage 
                // 
                localStorage.setItem('KonomiTV-Settings', JSON.stringify(settings));

                // 
                await Utils.syncClientSettingsToServer();

                // 
                this.$message.success('');
                window.setTimeout(() => this.$router.go(0), 300);

            } catch (error) {
                this.$message.error('');
                return;
            }
        },
    }
});

